.site-header {
  padding: 10px 0px;
  position: absolute;
  z-index: 2;
  transition: background-color 0.3s ease-out;
  @mixin atMedium {
    background-color: rgba($mainBlue, 0.3);
  }



  &--is-expanded {
    background-color: rgba($mainBlue, 0.8);
    @mixin atMedium {
      background-color: rgba($mainBlue, 0.3);
    }
  }

  width: 100%;

  &__btn-container {
    @mixin atMedium {
      float: right;
    }
  }

  &__logo {
    position: absolute;
    top: 0px;
    background-color: $mainBlue;
    padding: 25px 36px;
    left: 50%;
    transform: translateX(-50%) scale(0.8);
    transform-origin: 50% 0%;
    @mixin atMedium {
      left: auto;
      transform: translateX(0);
    }
  }

  &__logo__graphic {
    text-indent: -9999px;
  }

  &__menu-icon {
    width: 20px;
    height: 19px;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 20;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 0px;
      left: 0;
      width: 20px;
      height: 3px;
      background-color: #FFFFFF;
      transform-origin: 0% 0%;
      transition: transform 0.3s ease-out;
    }

    &__middle {
      position: absolute;
      top: 8px;
      left: 0;
      width: 20px;
      height: 3px;
      background-color: #FFFFFF;
      transition: all 0.3s;
      transform-origin: 0% 50%;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0px;
      left: 0;
      width: 20px;
      height: 3px;
      background-color: #FFFFFF;
      transform-origin: 0% 100%;
      transition: transform 0.3s ease-out;
    }

    @mixin atMedium {
      display: none;
    }
  }

  &__menu-icon--close-x {
    &::before {
      transform: rotate(45deg) scaleX(1.25);
    }
    .site-header__menu-icon__middle {
      opacity: 0;
      transform: scaleX(0);
    }
    &::after {
      transform: rotate(-45deg) scaleX(1.25) translateY(1px);
    }
  }

  &__menu-content {
    opacity: 0;
    transform: scale(1.2);
    transition: all 0.3s ease-out;
    position: relative;
    z-index: -10;
    padding-top: 90px;
    text-align: center;

    @mixin atMedium {
      opacity: 1;
      z-index: 1;
      padding-top: 0px;
      transform: scale(1);
    }

    &--is-visible {
      opacity: 1;
      z-index: 1;
      transform: scale(1);
    }
  }




}







